
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}
html {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  color: #484848;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  margin: 0;
}
a {
  color: #317fa9;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
img {
  width: 100%;
}
iframe {
  width: 100%;
}
.body-container {
  position: relative;
  max-width: 1040px;
  margin: 0 auto;
  padding: 0 18px;
}
@media (min-width: 744px) {
.body-container {
    padding: 0 30px;
}
.body-container .body-contents {
    min-height: 400px;
}
}
@media (min-width: 1128px) {
.body-container {
    padding: 0;
}
.body-container .body-contents {
    min-height: 500px;
}
}
.input-basic {
  border-radius: 3px;
  border: 1px solid #dbdbdb;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 8px 12px;
  outline: none;
}
.input-basic[type="file"] {
  display: none;
}
.input-basic:disabled {
  background-color: #efefef;
}
.input-none-border {
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.textarea-basic {
  resize: vertical;
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
}
.upload-label-basic {
  padding: 10px 28px;
  border-radius: 3px;
  cursor: pointer;
}
.select-basic {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 8px 12px;
}
.modal-background {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 9997;
}
.modal-container {
  display: none;
  position: fixed;
  top: calc(30%);
  left: calc(40%);
  width: 320px;
  height: 145px;
  background: #FFF;
  border-radius: 3px;
  z-index: 9998;
  padding: 10px;
}
.modal-container .modal-contents {
  position: relative;
  height: 100%;
}
.modal-container .modal-contents .title {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}
.modal-container .modal-contents .button-container {
  width: 100%;
  display: table;
  text-align: center;
  vertical-align: middle;
  height: 100px;
}
.modal-container .modal-contents .button-container button {
  margin: 0 10px 8px 10px;
}
.modal-container .modal-contents .button-container .left-button-container {
  display: table-cell;
  vertical-align: bottom;
  width: 50%;
}
.modal-container .modal-contents .button-container .right-button-container {
  display: table-cell;
  vertical-align: bottom;
  width: 50%;
}
.divider {
  margin: 18px 0 30px 0;
  width: 100%;
  height: 1px;
  background-color: #dbdbdb;
  display: block;
}
@-webkit-keyframes TextFadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes TextFadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes TextFadeOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes TextFadeOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
::-webkit-input-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
::-moz-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
/* firefox 19+ */
:-ms-input-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
/* ie */
input:-moz-placeholder {
  color: #bcbcbc;
  font-weight: 200;
}
#app {
  font-family: 'Roboto', sans-serif;
  color: #484848;
  background-color: #FFF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  outline: none;
}
a {
  font-weight: 400;
  color: #317fa9;
}
a:hover {
  cursor: pointer;
  color: #317fa9;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
img {
  width: 100%;
}
iframe {
  width: 100%;
  height: 180px;
}
input {
  border-radius: 3px;
  border: 1px solid #dbdbdb;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 8px 12px;
  outline: none;
}
input[type="file"] {
  display: none;
}
input:disabled {
  background-color: #efefef;
}
textarea {
  resize: vertical;
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
}
select {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 8px 12px;
}
#ui-datepicker-div {
  background-color: #FFF !important;
  border: 1px solid #dbdbdb !important;
  width: 300px;
}
.button-orange {
  border: none;
  background-color: #f2583d;
  color: #FFF;
  border-radius: 3px;
  padding: 10px 28px;
  outline: none;
}
.button-orange:hover {
  background-color: #f2583d;
  color: #FFF;
}
.button-orange:focus {
  background-color: #f2583d;
  color: #FFF;
}
.button-orange:active {
  background-color: #f2583d;
  color: #FFF;
}
.button-white {
  border: 1px solid #f2583d;
  background-color: #FFF;
  color: #f2583d;
  border-radius: 3px;
  padding: 10px 28px;
  outline: none;
}
.button-white:hover {
  background-color: #FFF;
  color: #f2583d;
}
.button-white:focus {
  background-color: #FFF;
  color: #f2583d;
}
.button-white:active {
  background-color: #FFF;
  color: #f2583d;
}
.btn,
.btn-lg {
  background-color: #f2583d;
  color: #FFF;
  border: none;
}
.btn:hover,
.btn-lg:hover {
  background-color: #f2583d;
  color: #FFF;
}
.btn:focus,
.btn-lg:focus {
  background-color: #f2583d;
  color: #FFF;
}
.gm-style-pbc {
  opacity: 0 !important;
}
@media (min-width: 744px) {
iframe {
    width: 100%;
    height: 440px;
}
.contents {
    padding: 0 24px;
}
.detail-contents {
    position: relative;
    padding: 0 24px;
}
.main-contents {
    position: relative;
    padding: 0 24px;
}
}
@media (min-width: 1128px) {
iframe {
    width: 100%;
    height: 544px;
}
.contents {
    padding: 0;
}
.detail-contents {
    position: relative;
    padding: 0;
}
.main-contents {
    position: relative;
    padding: 0;
}
}



