
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
}
html {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  color: #484848;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  margin: 0;
}
a {
  color: #317fa9;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
img {
  width: 100%;
}
iframe {
  width: 100%;
}
.body-container {
  position: relative;
  max-width: 1040px;
  margin: 0 auto;
  padding: 0 18px;
}
@media (min-width: 744px) {
.body-container {
    padding: 0 30px;
}
.body-container .body-contents {
    min-height: 400px;
}
}
@media (min-width: 1128px) {
.body-container {
    padding: 0;
}
.body-container .body-contents {
    min-height: 500px;
}
}
.input-basic {
  border-radius: 3px;
  border: 1px solid #dbdbdb;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding: 8px 12px;
  outline: none;
}
.input-basic[type="file"] {
  display: none;
}
.input-basic:disabled {
  background-color: #efefef;
}
.input-none-border {
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.textarea-basic {
  resize: vertical;
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
}
.upload-label-basic {
  padding: 10px 28px;
  border-radius: 3px;
  cursor: pointer;
}
.select-basic {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 8px 12px;
}
.modal-background {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 9997;
}
.modal-container {
  display: none;
  position: fixed;
  top: calc(30%);
  left: calc(40%);
  width: 320px;
  height: 145px;
  background: #FFF;
  border-radius: 3px;
  z-index: 9998;
  padding: 10px;
}
.modal-container .modal-contents {
  position: relative;
  height: 100%;
}
.modal-container .modal-contents .title {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}
.modal-container .modal-contents .button-container {
  width: 100%;
  display: table;
  text-align: center;
  vertical-align: middle;
  height: 100px;
}
.modal-container .modal-contents .button-container button {
  margin: 0 10px 8px 10px;
}
.modal-container .modal-contents .button-container .left-button-container {
  display: table-cell;
  vertical-align: bottom;
  width: 50%;
}
.modal-container .modal-contents .button-container .right-button-container {
  display: table-cell;
  vertical-align: bottom;
  width: 50%;
}
.divider {
  margin: 18px 0 30px 0;
  width: 100%;
  height: 1px;
  background-color: #dbdbdb;
  display: block;
}
@-webkit-keyframes TextFadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes TextFadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes TextFadeOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes TextFadeOut {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.body-contents {
  margin-top: 20px;
}
.dashboard-page-container {
  position: relative;
}
.dashboard-page-container .header-container .title {
  font-size: 30px;
  font-weight: 600;
}



